/*
header #3b5998
blue medium #8b9dc3
blue light #dfe3ee	
background #f7f7f7 - light white
text #111111
*/

* {
    text-decoration: none;
}

header {
    top: 0;
    left: 0;
    right: 0;
    background-color: #3b5998;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: fixed;
    z-index: 999;
    width: 100%;
    padding: 0 30px;
}

.header--logo {
    font-size: 30px;
    text-shadow: 0 0 4px #8b9dc3, 0 0 4px #fff;
}

.header--logo a, Link {
    color: #f7f7f7;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
}

.header--menu {
    list-style: none;
    margin-left: 30px;
}

.header--menu a {
    font-size: 20px;;
    color: #f7f7f7;
    padding-left: 15px;
}

.header--menu a:hover {
    text-shadow: 0 0 4px #fff, 0 0 4px #8b9dc3;
    transition: all ease 0.2s;
}

.dropdown {
    float: left;
    overflow: hidden;
}

.dropdown .dropbtn {
    font-size: 17px;    
    border: none;
    outline: none;
    color: white;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    background-color: #f7f7f7;
}

.dropdown-content a {
    float: none;
    color: black;
    padding: 5px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
    font-size: 16px;
}
  
.topnav a:hover, .dropdown:hover .dropbtn {
   background-color: #8b9dc3;
   color: white;
 }
  
.dropdown-content a:hover {
   background-color: #ddd;
   color: black;
}
  
.dropdown:hover .dropdown-content {
   display: block;
}

.search form input[type=text] {
    border-radius: 5px;
    border: none;
    padding: 8px;
    margin: 5px;
    outline: none;
    color: black;
}